* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
}

.post {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* margin-top: 15px; */
}

.post-detail {
	/* margin: 20px; */
	max-width: 550px;
	border-radius: 10px;
}
.post-detail h4 {
	/* padding: 5px 10px; */
}

.btn {
	padding: 5px 10px;
	text-align: center;
	font-weight: 700;
	/* font-size: 10px; */
	color: #f9fafb;
	/* border-radius: 0; */
}

/* .btn:hover { */
/* 	opacity: 0.9; */
/* 	transform: scale(1.085); */
/* } */

.btn span {
	display: block;
	/* font-weight: 300; */
	/* font-size: 8px; */
}

.post-btn {
	/* margin: 10px 10px; */
}

.left {
	float: left;
}
.right {
	float: right;
}

.upvote {
	background-color: #60a5fa;
	/* border-bottom-left-radius: 10px; */
	/* border-top-left-radius: 10px; */
}
.downvote {
	background-color: #f87171;

	/* border-bottom-right-radius: 10px; */
	/* border-top-right-radius: 10px; */
}
.comment {
	background-color: #4b5563;
	color: #d1d5db;
	/* border-radius: 10px; */
}

h4 span {
	/* text-decoration: underline; */
	color: #d1d5db;
	padding: 5px 7px;
	font-weight: 400;
	font-size: 12px;
}

.title {
	color: white;
	font-weight: 600;
	font-size: 15px;
	/* margin: 5px; */
}

.leader-board {
	width: 300px;
	border-radius: 10px;
	text-align: center;
	background-color: #374151;
	/* margin: 26px 40px; */
}

.top {
	background-color: #f87171;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	color: white;
	padding: 10px;
}

.bottom {
	background-color: #60a5fa;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	color: white;
	padding: 10px;
}

.subreddit {
	background-color: #4b5563;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 10px 40px;
}

.front-page {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
}

.bottom:hover {
	cursor: pointer;
}

.subreddit h4 {
	color: #f9fafb;
}

.text-span {
	color: #d1d5db;
	font-size: 12px;
}

img {
	width: 100%;
	margin: 10px 0px;
}
/* Hide scrollbar for Chrome, Safari and Opera */
#comments::-webkit-scrollbar {
	display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
#comments {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}

@media all and (max-width: 623px) {
	.leader-board {
		display: none;
	}
}
